.header
    padding-top: 51px
    height: 34px
    .container 
        display: flex
    .logo
        display: flex
        font-size: 32px
        .line
            width: 19px
            height: 4px
            margin-top: auto
            margin-bottom: auto
            margin-right: 11px
            background-color: red

.burger
    display: flex
    flex-direction: column
    margin-left: auto
    .line
        width: 48px
        height: 4px
        margin-top: 8px
        background-color: black
    &:hover
        .line:nth-child(1) 
            background-color: red